.userList{
    flex: 4;
    padding: 20px;
}
.userListUser{
    display: flex;
    align-items: center;
}
.userListImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.userListEdit{
    border-radius: 10px;
    border: none;
    padding: 5px 10px;
    background-color: darkblue;
    color: white ;
    cursor: pointer;
    margin-right: 12px;

}
.userListDelete{
    color: red;
    font-size: 20px;
}