.sidebar {
    flex: 1;
    background-color: lightgray;
    height: calc(150vh - 60px);
    color: white;
    position: sticky;
    top: 60px;
}

.sidebarWapper {
    color: rgb(36, 35, 35);
}

.sidebarManu {
    margin-bottom: 12px;
}

.sidebarTitle {
    text-align: center;
    margin-top: 10px;
    font-family: 'Lora', serif;
    color: darkblue;
}

.sidebarList {
    list-style: none;
}

.sidebarListItems {
    margin-top: 5px;
    font-size: 15px;
    display: flex;
    align-items: center;
    padding: 5px;
    border-radius: 10px;
    cursor: pointer;
    margin-right: 5px;
    color: darkblue;
}

.sidebarListItems i {
    color: white;
    margin-right: 15px;
    text-align: left;
    font-size: 20px;
}

.sidebarListItems.active,
.sidebarListItems:hover {
    background-color: rgb(151, 151, 233);
}